@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #f5f5f5;
  font-family: 'Nunito Sans', sans-serif;
}

#root {
  max-width: 1140px;
  margin: 0 auto;
}

button {
  cursor: pointer;
  height: 30px;
  border: none;
  outline: none;
  border-radius: 5px;
  font-family: 'Nunito Sans', sans-serif;
}
.content {
  min-width: 100%;
}

.catalog {
  display: flex;
  justify-content: space-between;
}

.content__search {
  width: 300px;
  height: 40px;
  margin-top: 20px;
  border: 2px solid black;
  font-size: 22px;
}

.content__btn {
  display: block;
  margin: 0 auto;
  margin-top: 20px;
  width: 200px;

  background-color: rgba(220, 0, 78, 0.8);
  color: #fff;
  text-transform: uppercase;
  font-weight: 700;
  box-shadow: 2px 2px 10px 5px rgba(0, 0, 0, 0.2);
}

.content__btn:hover {
  -webkit-filter: brightness(90%);
          filter: brightness(90%);
}

@media (max-width: 720px) {
  .content__search {
    margin: 10px;
  }
}

.beer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  padding: 16px;
  min-height: 30px;
  border-radius: 5px;
  margin-top: 8px;
  margin-left: auto;
  margin-right: auto;
  width: 90%;
}

.beer:hover {
  box-shadow: 2px 2px 10px 5px rgba(0, 0, 0, 0.2);
}

.beer__name {
  text-transform: uppercase;
  color: #fff;
  font-size: 20px;
}

.beer__btn {
  display: none;
  width: 60px;
}

.beer__btn:hover {
  transform: scale(110%);
}

.beer:hover .beer__btn {
  display: block;
}

@media (max-width: 720px) {
  .beer {
    width: 80%;
    margin: 10px auto;
    min-height: 40px;
    padding: 10px;
  }

  .beer__name {
    font-size: 22px;
  }
}

.column {
  flex: 0 1 45%;
}

.column__item {
  margin: 10px 0;
  padding: 4px;  
  height: 80vh;
  overflow-y: scroll;
}

.column__btn {
  cursor: pointer;
  color: #fff;
  width: 200px;
  height: 30px;
  margin-top: 20px;
  text-transform: uppercase;
  font-family: 'Nunito Sans', sans-serif;
  outline: none;
  border: none;
  border-radius: 5px;
  font-weight: 700;
}

.column__btn:hover {
  -webkit-filter: brightness(90%);
          filter: brightness(90%);
  box-shadow: 0 0.3rem rgba(255, 255, 255, 0.7);
}

.column__btn:active {
  transform: translate(0, 2px);
}

.column__btn:disabled {
  cursor: default;
  opacity: 0.4;
}


.btn__beer {
background-color: rgba(25, 118, 210, 0.8);
}

.btn__basket {
  background-color: rgba(220, 0, 78, 0.8);
}

@media (max-width: 720px) {
  .column {
    margin: 10px 10px;
    min-width: 130px;
  }
  .column__btn {
    width: 120px;
  }
}


.loader {
  margin: 200px auto;
  border: 16px solid #f3f3f3;
  border-top: 16px solid #3498db;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg);}
  100% {transform: rotate(360deg);}
}
.header {
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #1c2970;
  margin: 0 auto;
}

.header__name {
  text-transform: uppercase;
  font-size: 20px;
  padding: 8px;
  color: #fff;
}

@media (max-width: 720px) {
  .header__name {
    font-size: 15px;
  }
}
.nav {
  display: flex;
  justify-content: space-between;
  flex: 0 0 30%;
}

.nav__link {
  text-transform: uppercase;
  font-size: 20px;
  text-decoration: none;
  padding: 6px;
  color: #fff;
}


@media (max-width: 720px) {
  .nav__link {
    font-size: 15px;
  }
}
