@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans&display=swap');

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #f5f5f5;
  font-family: 'Nunito Sans', sans-serif;
}

#root {
  max-width: 1140px;
  margin: 0 auto;
}

button {
  cursor: pointer;
  height: 30px;
  border: none;
  outline: none;
  border-radius: 5px;
  font-family: 'Nunito Sans', sans-serif;
}
.content {
  min-width: 100%;
}

.catalog {
  display: flex;
  justify-content: space-between;
}

.content__search {
  width: 300px;
  height: 40px;
  margin-top: 20px;
  border: 2px solid black;
  font-size: 22px;
}

.content__btn {
  display: block;
  margin: 0 auto;
  margin-top: 20px;
  width: 200px;

  background-color: rgba(220, 0, 78, 0.8);
  color: #fff;
  text-transform: uppercase;
  font-weight: 700;
  box-shadow: 2px 2px 10px 5px rgba(0, 0, 0, 0.2);
}

.content__btn:hover {
  filter: brightness(90%);
}

@media (max-width: 720px) {
  .content__search {
    margin: 10px;
  }
}
